/* HTML: <div class="loader2"></div> */
.loader2 {
    width: 8vw;
    display: grid;
    --mask: 
      radial-gradient(12px at left 15px top 50%, #0000 95%, #000),
      radial-gradient(12px at right 15px top 50%, #0000 95%, #000);
    -webkit-mask: var(--mask);
            mask: var(--mask);
    -webkit-mask-composite: source-in;
            mask-composite: intersect;
    animation: l1 1s infinite alternate;
  }
  .loader2:before,
  .loader2:after {
    content: "";
    grid-area: 1/1;
    height: 40px;
    aspect-ratio: 1;
    background: #fff;
    border-radius: 50%;
  }
  .loader2:after {
    margin-left: auto;
  }
  @keyframes l1 {
    to {
      width: 40px;
    }
  }